/* 视频容器布局 */
.video-gallery {
  display: flex;
  justify-content: space-between;
  gap: 2px; /* 让视频之间的间隙非常小 */
  width: 99vw; /* 容器宽度占满整个视口 */
  height: auto; /* 容器高度占满整个视口 */
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* 视频 + 图例的容器 */
.video-item-container {
  display: flex;
  flex-direction: column; /* 垂直排列：视频在上，图例在下 */
  align-items: center;
  text-align: center;
  width: calc(100% / 3); /* 让三个视频均分屏幕 */
  height: auto; /* 让容器占满高度 */
}

.video-gallery-grey {
  width: 100%;
  background-color: rgb(248, 248, 248);
  padding: 20px;
  box-sizing: border-box;
  margin: 0 auto;
  text-align: center;
}
.video-item-full-container {
  display: inline-block;
  width: 50%;
  box-sizing: border-box;
}
.video-full-item {
  width: 100%;
  height: auto;
}
.video-gallery-white {
  width: 100%;
  /* background-color: rgb(248, 248, 248); */
  padding: 20px;
  box-sizing: border-box;
  margin: 0 auto;
  text-align: center;
}

/* 视频样式 */
.video-item {
  width: 100%;
  height: calc(100% - 20px); /* 视频占满容器，留点空间给图例 */
  object-fit: cover; /* 保证视频填充整个框 */
}

/* 单个视频下的图例 */
figcaption {
  font-size: 14px;
  font-style: italic;
  color: gray;
  text-align: center;
  height: 20px; /* 统一高度，确保对齐 */
  line-height: 20px; /* 居中文本 */
  width: 100%;
  background: rgba(255, 255, 255, 0.8); /* 半透明背景，提高可读性 */
}

/* 整体图例（如果需要） */
.video-caption {
  display: true; /* 如果你不需要整体图例，可以隐藏 */
}

/* 响应式设计：屏幕较小时，每个视频占满整行 */
@media (max-width: 768px) {
  .video-gallery {
      flex-direction: column;
      height: auto; /* 高度自适应 */
  }
  
  .video-item-container {
      width: 100%; /* 每个视频占满整个屏幕宽度 */
      height: auto;
  }

  .video-item {
      height: auto;
  }
}

.overlay-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 2rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); /* Optional shadow for better visibility */
  z-index: 1; /* Ensure text is on top of the video */
}