body {
  font-family: Noto Sans, sans-serif;
}

.footer .icon-link {
  color: #000;
  font-size: 25px;
}

.link-block a {
  margin-top: 5px;
  margin-bottom: 5px;
}

.dnerf {
  font-variant: small-caps;
}

.teaser .hero-body {
  padding-top: 0;
  padding-bottom: 3rem;
}

.teaser {
  font-family: Google Sans, sans-serif;
}

.publication-banner {
  max-height: parent;
}

.publication-banner video {
  object-fit: fit;
  position: relative;
  top: auto;
  left: auto;
  transform: none;
}

.publication-title, .publication-authors {
  font-family: Google Sans, sans-serif;
}

.publication-venue {
  color: #555;
  width: fit-content;
  font-weight: bold;
}

.publication-awards {
  color: #ff3860;
  width: fit-content;
  font-weight: bolder;
}

.publication-authors a {
  color: #209cee !important;
}

.publication-authors a:hover {
  text-decoration: underline;
}

.author-block {
  display: inline-block;
}

.publication-video {
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
  position: relative;
  overflow: hidden;
  border-radius: 10px !important;
}

.publication-video iframe {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.results-carousel {
  overflow: hidden;
}

.results-carousel .item {
  border: 1px solid #bbb;
  border-radius: 10px;
  margin: 5px;
  padding: 0;
  font-size: 0;
  overflow: hidden;
}

.results-carousel video {
  margin: 0;
}

.interpolation-panel {
  background: #f5f5f5;
  border-radius: 10px;
}

.interpolation-panel .interpolation-image {
  border-radius: 5px;
  width: 100%;
}

.interpolation-panel .slider {
  margin: 0 !important;
}

#interpolation-image-wrapper {
  width: 100%;
}

#interpolation-image-wrapper img {
  border-radius: 5px;
}

.video-gallery {
  box-sizing: border-box;
  justify-content: space-between;
  gap: 2px;
  width: 99vw;
  height: auto;
  margin: 0;
  padding: 0;
  display: flex;
}

.video-item-container {
  text-align: center;
  flex-direction: column;
  align-items: center;
  width: 33.3333%;
  height: auto;
  display: flex;
}

.video-gallery-grey {
  box-sizing: border-box;
  text-align: center;
  background-color: #f8f8f8;
  width: 100%;
  margin: 0 auto;
  padding: 20px;
}

.video-item-full-container {
  box-sizing: border-box;
  width: 50%;
  display: inline-block;
}

.video-full-item {
  width: 100%;
  height: auto;
}

.video-gallery-white {
  box-sizing: border-box;
  text-align: center;
  width: 100%;
  margin: 0 auto;
  padding: 20px;
}

.video-item {
  object-fit: cover;
  width: 100%;
  height: calc(100% - 20px);
}

figcaption {
  color: gray;
  text-align: center;
  background: #fffc;
  width: 100%;
  height: 20px;
  font-size: 14px;
  font-style: italic;
  line-height: 20px;
}

.video-caption {
  display: true;
}

@media (width <= 768px) {
  .video-gallery {
    flex-direction: column;
    height: auto;
  }

  .video-item-container {
    width: 100%;
    height: auto;
  }

  .video-item {
    height: auto;
  }
}

.overlay-text {
  color: #fff;
  text-shadow: 2px 2px 4px #00000080;
  z-index: 1;
  font-size: 2rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
